@use 'variables' as v;
@use 'mixins' as m;

h1, h2, h3 {
    @include m.headings;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
.text-center {
    text-align: center;
}

[class$="__heading"] {  //Cualquier elemento que termine en __heading
    // ^= "" Cualquier elemento que empiece con
    // *= "" Cualquier elemento que contenga...
    @include m.nombrePagina;
}

[class$="descripcion"] { 
    @include m.descipcion-pagina;
}
