@use 'base/variables' as v;
@use 'base/mixins' as m;
@use "sass:color";


.alertas {
    padding: 1.5rem 1.5rem 1.5rem 3rem;
    text-transform: uppercase;
    margin: 1rem 0;
    font-size: 1.2rem;
    font-weight: v.$bold;

    &__error {
        background-color: color.scale( v.$rojo, $lightness: 60%);
        border-left: .5rem solid v.$rojo;
        color: v.$rojo;
    }

    &__exito {
        background-color: color.scale( v.$verde, $lightness: 40%);
        border-left: .5rem solid v.$verde;
        color: v.$verde;
    }
}